.posts{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 5px;
}

.pic{
    border-radius: 50%;
    width: 64px;
}

.row{
    display: flex;
    justify-content: left;
    align-items: center;
}

.name{
    font-weight: bold;
}

.dash{
    border-top: 1px solid #d3d3d3;
    background-color: #fff;
}

.digits{
    font-weight: bold;
    display: flex;
    justify-content: space-evenly;
}

.labels{
    font-weight:lighter;
    display: flex;
    justify-content: space-evenly;
}

